#board {
    display: grid;
    gap: 8px; 
    justify-content: center; 
  }

  .tile {
    width: 60px;
    height: 60px;
    border: 2px solid #d3d6da;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
    font-weight: bold;
    text-transform: uppercase;
    background-color: white;
    color: #000;
    transition: background-color 0.3s, border-color 0.3s;
    box-sizing: border-box;
  }
  
  